import React, { Component } from 'react';
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";
import "./BusinessName.css"
import { Form, Button } from "react-bootstrap";
import { Link, navigation, navigate } from "react-router-dom";
import payment from '../assets/BillingPage.png'
import card from '../assets/Capture9.PNG'
// import Calendar from 'react-input-calendar'
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import "./BusinessName.css"


class BusinessName extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        businessName: "",
        businessAddress: "",
        name: "",
        mobileNumber: "",
        cardNumber: "",
        cvv: "",
        expiry: "",
        step: 1,
        iserror: false,
        errormsg: "no error",
        isloading: false,
        agree: false
    };

    componentDidMount = () => {




        function getScrollHeight(elm) {
            var savedValue = elm.value
            elm.value = ''
            elm._baseScrollHeight = elm.scrollHeight
            elm.value = savedValue
        }

        function onExpandableTextareaInput({ target: elm }) {
            // make sure the input event originated from a textarea and it's desired to be auto-expandable
            if (!elm.classList.contains('autoExpand') || !elm.nodeName == 'TEXTAREA') return

            var minRows = elm.getAttribute('data-min-rows') | 0, rows;
            !elm._baseScrollHeight && getScrollHeight(elm)

            elm.rows = minRows
            rows = Math.ceil((elm.scrollHeight - elm._baseScrollHeight) / 30)
            elm.rows = minRows + rows
        }


        // global delegated event listener
        document.addEventListener('input', onExpandableTextareaInput)




        // OLD SOLUTION USING JQUERY:
        // // Applied globally on all textareas with the "autoExpand" class

        // $(document)
        //     .one('focus.autoExpand', 'textarea.autoExpand', function(){
        //         var savedValue = this.value;
        //         this.value = '';
        //         this._baseScrollHeight = this.scrollHeight;
        //         this.value = savedValue;
        //     })
        //     .on('input.autoExpand', 'textarea.autoExpand', function(){
        //         var minRows = this.getAttribute('data-min-rows')|0, rows;
        //         this.rows = minRows;
        //         rows = Math.ceil((this.scrollHeight - this.baseScrollHeight) / 16);
        //         this.rows = minRows + rows;
        //     });




        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);
    }

    routeChange = () => {
        let path = `/account/BusinessAddress`;
        this.props.history.push(path);
    }

    handleChange = (event) => {
        if (event.target.id === "name") {
            const name = event.target.value;
            this.setState({ name: name });
        } else if (event.target.id === "mobile") {
            const mobile = event.target.value;
            this.setState({ mobileNumber: mobile });
            if (Math.round(mobile) != mobile) {
                alert("Variable is not an integer! Please Enter an integer number");
            }
        } else if (event.target.id === "businessName") {
            const businessName = event.target.value;
            this.setState({ businessName: businessName });
        } else if (event.target.id === "businessAddress") {
            const businessAddress = event.target.value;
            this.setState({ businessAddress: businessAddress });
        } else if (event.target.id === "agree") {
            this.setState({ agree: event.target.checked });
        }
    };

    continueName = () => {
        if (document.getElementById("businessName",).value.length == 0) {
            alert("Please Fill All Required Field");
            return false;
        }
        if (this.state.step == 1) {

        }
        this.setState({
            step: this.state.step + 1
        })
    }
    continueAddress = () => {
        if (document.getElementById("businessAddress").value.length == 0) {
            alert("Please Fill All Required Field");
            return false;
        }
        if (this.state.step == 1) {

        }
        this.setState({
            step: this.state.step + 1
        })
    }
    continueIncharge = () => {
        if (document.getElementById("mobile", "name").value.length == 0) {
            alert("Please Fill All Required Field");
            return false;
        }
        if (document.getElementById("mobile").value.length != 11) {
            alert("Phone Number is Invalid");
            return false;
        }
        if (this.state.step == 1) {

        }
        this.setState({
            step: this.state.step + 1
        })
    }
    back = () => {
        this.setState({
            step: this.state.step - 1
        })
    }

    complete = () => {
        this.setState({ isloading: true });
        if (this.state.agree) {
            var user_details = Cookies.get("user_details");
            user_details = JSON.parse(user_details);
            console.log('user details', user_details);
            console.log('user ID', user_details.id);
            var action = 'registerBusinessUser';
            var body = JSON.stringify({
                user_id: user_details.id,
                name: this.state.businessName,
                address: this.state.businessAddress,
                incharge_name: this.state.name,
                incharge_mobile_number: this.state.mobileNumber
            });
            apiCall(action, body).then((result) => {
                console.log("register", result)
                let responseJSON = result;
                if (responseJSON.error) {
                    this.setState({
                        iserror: true,
                        errormsg: responseJSON.message,
                    });
                } else {
                    if (responseJSON.data) {
                        console.log("user register respone", responseJSON.data)
                        localStorage.setItem("user_registered", responseJSON.data);
                        Cookies.set("user_registered", responseJSON.data, { expires: 1 });
                        let path = `/account/BillingInfo`;
                        this.props.history.push(path);
                    }
                }
                this.setState({ isloading: false });
            })
        }
        else {
            this.setState({
                iserror: true,
                errormsg: "Accept the agreement first",
                isloading: false
            });
        }

    }



    render() {
        const { iserror, errormsg, isloading } = this.state;
        if (this.state.step === 1) {
            return (
                <div className="container">
                    <div className="row mt-5 ">
                        <div className="col-lg-1"></div>
                        <div className="col-lg-10 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <h4 style={{ fontWeight: "bold" }}>Business <br /> name</h4>
                                    </div>
                                </div>
                                <Form className="mt-5">
                                    <Form.Group required controlId="businessName">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "1px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} type="name" placeholder="business name" name="businessName" required value={this.state.businessName} onChange={this.handleChange} />
                                    </Form.Group>
                                    <Button onClick={this.continueName} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 160 }} >
                                        Continue
                                </Button>
                                </Form>
                            </div>
                        </div>
                        <div className="col-lg-1"></div>

                    </div>
                </div>

            )
        } else if (this.state.step === 2) {
            return (
                <div className="container">
                    <div className="row ">
                        <div className="col-lg-1"></div>
                        <div className="col-lg-10 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link><h4 style={{ fontWeight: "bold", marginTop: "10px", }}>Business <br /> address</h4>
                                    </div>
                                </div>
                                {/* <Form className="mt-5"> */}
                                {/* <Form.Group controlId="businessAddress"> */}
                                <textarea id="businessAddress" style={{ width: "100%", }} className='autoExpand' rows='1' data-min-rows='1' type="name" placeholder="business address" required name="businessAddress" value={this.state.businessAddress} onChange={this.handleChange} ></textarea>
                                {/* </Form.Group> */}
                                <Button onClick={this.continueAddress} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 160 }} >
                                    Continue
                                </Button>
                                {/* </Form> */}
                            </div>
                        </div>
                        <div className="col-lg-1"></div>

                    </div>
                </div>
            )
        } else if (this.state.step === 3) {
            return (
                <div className="container">
                    <div className="row ">
                        <div className="col-lg-1"></div>
                        <div className="col-lg-10 col-sm-12">
                            <div className="col">
                                <div className="row">
                                    <div className="col-12">
                                        <Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link><h4 style={{ fontWeight: "bold", marginTop: "10px" }}>Person in-charge <br /> contact</h4>
                                    </div>
                                </div>
                                <Form className="mt-5">
                                    <Form.Group controlId="name">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "1px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} type="name" placeholder="name" required name="name" value={this.state.name} onChange={this.handleChange} />
                                    </Form.Group>
                                    <Form.Group className="mt-5" controlId="mobile">
                                        <Form.Control style={{
                                            padding: "0px",
                                            fontSize: "20px",
                                            borderRight: "none",
                                            borderLeft: "none",
                                            borderTop: "none",
                                            borderRadius: "0px",
                                            borderBottomWidth: "1px",
                                            borderBottomColor: "black",
                                            boxShadow: "none"
                                        }} type="text" placeholder="mobile number" maxLength="11" required name="mobileNumber" value={this.state.mobileNumber} onChange={this.handleChange} />
                                    </Form.Group>
                                    <Button onClick={this.continueIncharge} className="btn" style={{ background: "#2101F0", color: "white", width: "100%", marginTop: 160 }} >
                                        Continue
                                </Button>
                                </Form>
                            </div>
                        </div>
                        <div className="col-lg-1"></div>
                    </div>
                </div>
            )
        } else if (this.state.step === 4) {
            return (
                <div className="container">
                    <div className="row ml-3 ">
                        <div className="col-lg-1"></div>
                        <div className="col-lg-10 col-sm-12">
                            <div className="row">
                                <div className="col-12">
                                    <Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link><h4 style={{ fontWeight: "bold", marginTop: "10px" }}>Pricing Plan</h4>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Free</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 0 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>0 - 100 customer base</span>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Essentials</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 65 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>101 - 500 customer base</span>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Standard</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 150 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>501 - 1k customer base</span>
                                </div>
                            </div>
                            <div className="row mt-4">
                                <div className="col-12">
                                    <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Premium</span> <span style={{ fontSize: 14, fontWeight: "500" }}>- RM</span> 250 monthly</div>
                                    <span style={{ fontSize: 20, color: "gray" }}>1k - unlimited customers</span>
                                </div>
                            </div>
                            <div className="row mt-5">
                                <div className="col">
                                    <h5 style={{ color: "gray" }}>Terms of service</h5>
                                    <hr className="for_hr" />
                                </div>
                            </div>
                            <div className="row mt-2 ml-1">
                                <input style={{ zoom: 1.5, fontSize: "12px", marginTop: "2px" }} type="checkbox" className="hidden" id="agree" readonly="" onChange={this.handleChange} tabindex="0" />

                                <span className="ml-1" style={{ fontSize: 12, color: "gray" }}>I have acknowledge to agree above all <br />pricing terms to utilise the platform</span>
                            </div>
                            <div className="row mt-1">
                                <div className="col-12 buttons">
                                    <button onClick={this.complete} className="btn " style={{ background: "#2101F0", color: "white", width: "240px" }} >
                                        I Agree
                                </button>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-1"></div>
                    </div>
                </div>
            )
        } else if (this.state.step === 5) {
            return (
                <div className="container">
                    <div className="row  mt-5 ">

                        <div className="col-lg-4 col-sm-12">
                            <div className="row">
                                <div className="col-12">
                                    <h4 style={{ fontWeight: "bold" }}><Link onClick={this.back} style={{ fontSize: "32px", color: "black" }}>&#8249;</Link> Billing info</h4>
                                </div>
                            </div>
                            <div className="row mt-2">
                                <div className="col-3">
                                    <img src={card} style={{ width: 60 }} />
                                </div>
                                <div className="col-3"></div>
                                <div className="col-6">
                                    <img src={payment} style={{ width: 130 }} />
                                </div>

                            </div>
                            {/* <div className="container">
                                    <div className="mt-5">
                                        <span style={{ fontSize: "18px", color: "gray" }}>Card number</span >
                                        <input style={{ width: "100%", borderRadius: "10px", borderColor: "gray" }} type="number" name="cardNumber" id="search" onChange={this.handleChange} />
                                    </div>
                                </div> */}
                            <Form className="mt-5">
                                <Form.Group controlId="cardNumber">
                                    <span style={{ fontSize: "14px", color: "gray" }}>Card number</span >
                                    <Form.Control style={{ fontSize: "16px", borderRadius: "10px" }} type="number" required name="cardNumber" onChange={this.handleChange} />
                                </Form.Group>
                                {/* <Form.Group className="container" controlId="cvv"> */}
                                <div className="row container">
                                    <span style={{ fontSize: "14px", color: "gray" }}>CVV</span >
                                    <span style={{ fontSize: "14px", color: "gray", marginLeft: 95 }}>Expiry</span >
                                </div>
                                {/* </Form.Group> */}
                                <Form.Group className="container" controlId="expiry">
                                    <div className="row">
                                        <Form.Control style={{ fontSize: "16px", borderRadius: "10px", width: "35%" }} type="name" required name="cvv" onChange={this.handleChange} />
                                        <Form.Control style={{ fontSize: "16px", borderRadius: "10px", width: "55%", marginLeft: 20 }} type="month" required name="expiry" onChange={this.handleChange} />
                                        {/* <Calendar format='DD/MM/YYYY' date='4-12-2014' /> */}
                                    </div>
                                </Form.Group>
                            </Form>
                            {/* <div className="container">
                                <div className=" mt-2">
                                    <span style={{ fontSize: "18px", color: "gray" }}>CVV</span >
                                    <span style={{ fontSize: "18px", color: "gray", marginLeft: 90 }}>Expiry</span >
                                </div>
                                <div className=" mt-2 mb-5">
                                    <input style={{ width: "40%", borderRadius: "10px", borderColor: "gray", }} type="text" name="CVV" onChange={this.handleChange} />
                                    <input style={{ width: "50%", borderRadius: "10px", borderColor: "gray", marginLeft: 20 }} name="expiry" type="number" placeholder="mm/yy" onChange={this.handleChange} />
                                </div>
                            </div> */}
                            <div className="pt-5">
                                <p style={{ fontSize: "14px" }}>It will automatically upgrade to Essentials
                                Plan when you exceed 100 customer base.
                            Flexible, no contract. Unsubscribe anytime.</p>
                            </div>
                            {iserror ? <p className="text-danger text-center">{errormsg}</p> : ""}
                            {isloading ? (
                                <Loader className="text-center" type="Oval" color="#323A40" height={50}
                                    width={50}
                                // timeout={3000} //3 secs
                                />
                            ) : (
                                    <div className=" text-center buttons">
                                        <button onClick={this.complete} className="btn " style={{ background: "#2101F0", color: "white", width: "100%", fontSize: "18px", borderRadius: "10px" }} >
                                            Complete
                                    </button>
                                    </div>
                                )}

                        </div>

                    </div>
                </div>
            )
        }
    }
}
export default BusinessName;