import React, { Component } from 'react';
import print from '../assets/print.png'
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";
import payment from '../assets/Capture.png'
import Footer from "../Footer/Footer";
import Loader from "react-loader-spinner";

class BillingCardInfo extends Component {

    constructor(props) {
        super(props);
    }
    state = {
        userData: "",
        paymentHistory: "",
        iserror: false,
        errormsg: "no error",
        isloading: true,
    };
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);

        var action = 'getUserData';
        var body = JSON.stringify({
            id: user_details.id === undefined ? user_details.user.id : user_details.id
        });
        apiCall(action, body).then((result) => {
            console.log("getUserData", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.props.history.push("/");
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data);
                    this.setState({ userData: responseJSON.data });
                }
            }
        })


        var action = 'paymentHistory';
        var body = JSON.stringify({
            biz_account_id: user_details.id === undefined ? user_details.user.id : user_details.id
        });
        apiCall(action, body).then((result) => {
            console.log("paymentHistory", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.props.history.push("/");
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data);
                    this.setState({ paymentHistory: responseJSON.data, isloading: false });
                }
            }
        })
    }
    render() {
        const { paymentHistory, userData, isloading } = this.state;
        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-12 col-sm-12">

                        <div className=" mt-5 mr-1 main-set">
                            {isloading ? (
                                <Loader style={{ marginTop: 240, marginLeft: 100 }}
                                    type="Oval"
                                    color="#323A40"
                                    height={100}
                                    width={100}
                                />
                            ) : (
                                    <div >
                                        <div>
                                            <h3 style={{ fontWeight: "bold" }}>Billing Info</h3>
                                        </div>
                                        <div>
                                            <img src={payment} style={{ width: 50 }} />
                                        </div>
                                        <div>
                                            <p style={{ fontSize: "16px", fontWeight: "500", marginTop: 10 }}>{paymentHistory.brand} <span style={{ color: "gray" }}>. . .{paymentHistory.last_four_digit}</span></p>
                                            <p style={{ fontSize: "16px", color: "gray" }}>This is your default payment method. </p>
                                        </div>
                                        <div className="mt-5">
                                            <a href="/user/BillingInfo" style={{ color: "black", textDecoration: "none" }}><p style={{ fontWeight: "bold", fontSize: "18px" }}>Update Payment Method <span className="arrow right"></span></p></a>
                                            <p style={{ fontSize: "16px", color: "gray" }}>Update payment method if you are
                                replacing default payment card.</p>
                                        </div>
                                        <div style={{ marginTop: "10%" }}>
                                            <img src={print} style={{ width: 80 }} />
                                        </div>
                                        <div className="mt-4">
                                            <p style={{ fontSize: "16px", color: "gray" }}> Last 3 PDF Bills </p>
                                        </div>
                                        {/* <div style={{ borderRadius: "5px", width: "160px", minWidth: "200px", backgroundColor: "#F1F2F2" }}>
                                <p style={{ padding: "10px", fontSize: "15px" }}>25 September 2020</p>
                            </div>
                            <div className="mt-0" style={{ borderRadius: "5px", width: "160px", minWidth: "200px", backgroundColor: "#F1F2F2" }}>
                                <p style={{ padding: "10px", fontSize: "15px" }}>25 September 2020</p>
                            </div>
                            <div className="mt-0" style={{ borderRadius: "5px", width: "160px", minWidth: "200px", backgroundColor: "#F1F2F2" }}>
                                <p style={{ padding: "10px", fontSize: "15px" }}>25 September 2020</p>
                            </div> */}
                                    </div>
                                )}
                        </div>
                    </div>
                </div>
                {/* <div style={{ marginTop: 140 }}>
                    <Footer />
                </div> */}
            </div>
        )
    }
}

export default BillingCardInfo;
