import React, { Component } from 'react';
// import "./BusinessName.css"
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Footer from "../Footer/Footer";


class PricingplanUser extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        userData: null,
        iserror: false,
        errormsg: "no error",
        isloading: true,
        agree: true
    }
    componentDidMount = () => {
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log(user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }
        user_details = JSON.parse(user_details);

        var action = 'getUserData';
        var body = JSON.stringify({
            id: user_details.id === undefined ? user_details.user.id : user_details.id
        });
        apiCall(action, body).then((result) => {
            console.log("getUserData", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.props.history.push("/");
            } else {
                if (responseJSON.data) {
                    console.log("dataa", responseJSON.data);
                    this.setState({ userData: responseJSON.data, isloading: false });
                }
            }
        })
    }
    routeChange = () => {
        console.log(this.state.userData);
        this.setState({ isloading: true });
        if (this.state.agree || true) {
            if (this.state.userData == null)
                var path = `/BillingInfo`;
            else
                var path = `/user/BillingInfo`;
            this.props.history.push(path);
        } else {
            this.setState({ iserror: true });
            this.setState({ errormsg: "Agree Terms of Services" });
            this.setState({ isloading: false });
        }
    }
    handleChange = (event) => {
        console.log(event.target.checked);
        this.setState({ agree: event.target.checked });
    }
    render() {
        const { iserror, errormsg, isloading } = this.state;
        return (
            <div className="container">
                <div className="row  mt-5 mr-4">
                    <div className="col-lg-12 col-sm-12 main-set">
                        {isloading ? (
                            <Loader style={{ marginTop: 240, marginLeft: 100 }}
                                type="Oval"
                                color="#323A40"
                                height={100}
                                width={100}
                            />
                        ) : (
                                <div>
                                    <div className="row">
                                        <div className="col-12">
                                            <h3 style={{ fontWeight: "bold" }}>Pricing Plan</h3>
                                        </div>
                                    </div>
                                    <div className="row mt-4">
                                        <div className="col-12">
                                            <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Free</span> <span style={{ fontSize: 14 }}>- RM</span> 0 monthly</div>
                                            <span style={{ fontSize: 20, color: "gray" }}>0 - 100 customer base</span>
                                        </div>
                                    </div>
                                    <div className="row mt-4">
                                        <div className="col-12">
                                            <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Essentials</span> <span style={{ fontSize: 14 }}>- RM</span> 65 monthly</div>
                                            <span style={{ fontSize: 20, color: "gray" }}>101 - 500 customer base</span>
                                        </div>
                                    </div>
                                    <div className="row mt-4">
                                        <div className="col-12">
                                            <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Standard</span> <span style={{ fontSize: 14 }}>- RM</span> 150 monthly</div>
                                            <span style={{ fontSize: 20, color: "gray" }}>501 - 1k customer base</span>
                                        </div>
                                    </div>
                                    <div className="row mt-4">
                                        <div className="col-12">
                                            <div style={{ fontSize: 20 }}><span style={{ fontWeight: "500", fontSize: 19, marginBottom: 0 }}>Premium</span> <span style={{ fontSize: 14 }}>- RM</span> 250 monthly</div>
                                            <span style={{ fontSize: 20, color: "gray" }}>1k - unlimited customers</span>
                                        </div>
                                    </div>
                                </div>
                            )}
                    </div>
                </div>
            </div>
        )
    }
}
export default PricingplanUser;