import React, { Component } from 'react';
import { Button } from 'react-bootstrap'
import './Verification.css'
import ReactCodeInput from 'react-verification-code-input';
import $ from "jquery";
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";


class Verification extends Component {
    state = {
        minutes: 2,
        seconds: 0,
        timer: 0
    }
    constructor(props) {
        super(props);
    }

    resend = () => {
        this.myInterval = setInterval(() => {
            const { seconds, minutes } = this.state

            if (seconds > 0) {
                this.setState(({ seconds }) => ({
                    seconds: seconds - 1
                }))
            }
            if (seconds === 0) {
                if (minutes === 0) {
                    clearInterval(this.myInterval)
                } else {
                    this.setState(({ minutes }) => ({
                        minutes: minutes - 1,
                        seconds: 59
                    }))
                }
            }
        }, 1000)
    }



    componentDidMount() {
        this.myInterval = setInterval(() => {
            const { seconds, minutes } = this.state

            if (seconds > 0) {
                this.setState(({ seconds }) => ({
                    seconds: seconds - 1
                }))
            }
            if (seconds === 0) {
                if (minutes === 0) {
                    clearInterval(this.myInterval)
                } else {
                    this.setState(({ minutes }) => ({
                        minutes: minutes - 1,
                        seconds: 59
                    }))
                }
            }
        }, 1000)

        $('.add')
            .find("input")
            .each(function (index, element) {
                $(element).css('margin', '5px');
                $(element).css('border', 'none');
                $(element).css('border-radius', '10px');
                $(element).css('width', '48px');
                $(element).css('height', '45x');
                $(element).css('border-color', 'F1F2F2');
                $(element).css('background-color', '#F1F2F2');
                $(element).css('font-weight', '700');
                $(element).css('font-size', '30px');
                $(element).css('font-family', 'Arial');
                $(element).css('color', 'black');
            })
    }

    componentWillUnmount() {
        clearInterval(this.myInterval)
    }
    click = () => {
        var user_details = Cookies.get("user_details");
        console.log(user_details, "users")
        user_details = JSON.parse(user_details);
        var action = 'resendVerificationCode';
        var body = JSON.stringify({
            user_id: user_details.id === undefined ? user_details.user.id : user_details.id
        });

        apiCall(action, body).then((result) => {
            console.log("resendddd code", result)
            let responseJSON = result;
            if (responseJSON.error) {
                this.setState({
                    iserror: true,
                    errormsg: responseJSON.message,
                });
            } else {
                console.log("resend code response", JSON.stringify(responseJSON.data));
                Cookies.set("user_details", JSON.stringify(responseJSON.data), { expires: 1 });
                this.props.history.push("/account/Verification");

            }
            this.setState({ minutes: 2 })
            this.resend();
        })




    }
    routeChange = (value) => {
        var data = Cookies.get('user_details');
        console.log(data, "data");
        data = JSON.parse(data);
        console.log(data.verification_code, value, "hehe");
        if (data.verification_code !== undefined && data.verification_code == value) {
            console.log("jhdshdkj", data.verification_code)
            let path = `/account/BusinessName`;
            this.props.history.push(path);
        }
    }

    render() {
        const { minutes, seconds, timer } = this.state
        return (
            <div className="container">
                <div className="row  mt-5 ">
                    <div className="col-lg-1 col-sm-12"></div>
                    <div className="col-lg-10 col-sm-12">
                        <div >
                            <h3 style={{ fontWeight: "bold" }}>Verification</h3>
                            <p className="mt-5">Kindly enter 5-digit pin</p>
                        </div>
                        <ReactCodeInput
                            fields={5}
                            onComplete={this.routeChange}
                            className="add"
                        />
                        <div className="mt-5">
                            <p style={{ fontSize: "14px", color: "gray" }}>5-digit verification pin has sent to your email.</p>

                            {minutes === 0 && seconds === 0
                                ? <p onClick={this.click} style={{ cursor: "pointer", fontSize: "14px", color: "blue" }}>Resend verification code</p>
                                : <p style={{ fontSize: "14px", color: "gray" }}> {minutes}:{seconds < 10 ? `0${seconds}` : seconds}</p>
                            }
                        </div>
                    </div>
                    <div className="col-lg-1 col-sm-12"></div>
                </div>
            </div>
        )
    }
}
export default Verification;