import React, { Component } from 'react';
// import './SideNav.css'
import card from '../assets/card.png';
import print from '../assets/print.png';
import Cookies from "js-cookie";
import { apiCall } from "../Services/Services";
import Loader from "react-loader-spinner";
import './Account.css'
import Footer from "../Footer/Footer";

class Account extends Component {

    constructor(props) {
        super(props);
    }

    state = {
        userData: null,
        isloading: true,
        isLoder: false
    }

    componentDidMount = () => {
        // this.setState({ isLoder: true })
        var user_details = Cookies.get("user_details");
        // user_details = JSON.parse(user_details);
        console.log("After Login", user_details);

        if (user_details === undefined || user_details == "") {
            this.props.history.push("/");
        }

        else {
            user_details = JSON.parse(user_details);

            var action = 'getUserData';
            var body = JSON.stringify({
                id: user_details.id === undefined ? user_details.user.id : user_details.id
            });
            apiCall(action, body).then((result) => {
                console.log("getUserData", result)
                let responseJSON = result;
                if (responseJSON.error) {
                    this.props.history.push("/");
                } else {
                    if (responseJSON.data) {
                        if (responseJSON.data.user === undefined && (responseJSON.data.name === undefined || responseJSON.data.name == "" || responseJSON.data.name == null)) {
                            var action = 'resendVerificationCode';
                            var body = JSON.stringify({
                                user_id: responseJSON.data.id
                            });

                            apiCall(action, body).then((result) => {
                                console.log("resend code", result)
                                let responseJSON = result;
                                if (responseJSON.error) {
                                    this.setState({
                                        iserror: true,
                                        errormsg: responseJSON.message,
                                    });
                                } else {
                                    console.log("resend code response", JSON.stringify(responseJSON.data));
                                    // localStorage.setItem("user_details", temp);
                                    Cookies.set("user_details", JSON.stringify(responseJSON.data), { expires: 1 });
                                    // console.log("after saving data", Cookies.get('user_details'));
                                    this.props.history.push("/account/Verification");

                                }
                                this.setState({ isloading: false });
                            })
                        } else if (responseJSON.data.card_wallet_id === null) {
                            this.props.history.push("/account/BillingInfo");
                        }

                        console.log("dataa", responseJSON.data);
                        this.setState({ isloading: false, userData: responseJSON.data });
                    }
                    else {
                        var action = 'resendVerificationCode';
                        var body = JSON.stringify({
                            user_id: user_details.id === undefined ? user_details.user.id : user_details.id
                        });

                        apiCall(action, body).then((result) => {
                            console.log("resendddd code", result)
                            let responseJSON = result;
                            if (responseJSON.error) {
                                this.setState({
                                    iserror: true,
                                    errormsg: responseJSON.message,
                                });
                            } else {
                                console.log("resend code response", JSON.stringify(responseJSON.data));
                                Cookies.set("user_details", JSON.stringify(responseJSON.data), { expires: 1 });
                                this.props.history.push("/account/home");
                            }
                            this.setState({ isloading: true });
                        })
                    }
                }
            })
        }
        // this.setState({ isLoder: true })

    }

    manage = () => {
        let path = `/user/Subscription`;
        this.props.history.push(path);
    }
    billings = () => {
        let path = `/user/BillingCardInfo`;
        this.props.history.push(path);
    }
    render() {
        const { userData, isloading, isLoder } = this.state;
        return (
            // isLoder ? (
            //     <Loader style={{ textAlign: "center" }}
            //         type="Oval"
            //         color="#323A40"
            //         height={200}
            //         width={200}
            //     />
            // ) : (


            <div className="container">
                <div className="row">
                    {/* <div className="col-lg-2 col-sm-12"></div> */}
                    <div className="col-lg-12 col-sm-12 ">

                        <div className=" main-set mt-5 ">
                            {isloading ? (
                                <Loader style={{ marginTop: 240, marginLeft: 100 }}
                                    type="Oval"
                                    color="#323A40"
                                    height={100}
                                    width={100}
                                />
                            ) : (
                                    <div>
                                        <div>
                                            <h3 style={{ fontWeight: "bold" }}>Account</h3>
                                        </div>
                                        <div className="mt-4">
                                            <p style={{ fontSize: 18, }}>Hello, {userData == null || userData.user === undefined ? "" : userData.user.name}!</p>
                                        </div>
                                        <div>
                                            <p style={{ fontSize: 16, fontWeight: "400", }}>You can manage your subscription and billing information here.</p>
                                        </div>
                                        <div className="mt-5">
                                            <img src={card} width="70px" height="70px" />
                                        </div>
                                        <div className="mt-3">
                                            <button onClick={this.manage} className="btn" style={{ color: "white", backgroundColor: "#2101F0", width: "220px" }}>Manage subscription</button>
                                        </div>
                                        <div className="mt-5">
                                            <img src={print} width="70px" height="70px" />
                                        </div>
                                        <div className="mt-3">
                                            <button onClick={this.billings} className="btn" style={{ color: "white", backgroundColor: "#2101F0", width: "220px" }}> Billing info</button>
                                        </div>
                                    </div>
                                )}
                        </div>


                    </div>
                </div>
            </div>
        )

    }


}

export default Account;
