import React, { Component } from 'react';
import Footer from '../Footer/Footer';
import SideNav from '../SideNav/SideNav';
import logo from "../assets/logo.png"
import './Header.css'
import Loader from "react-loader-spinner";

class Header extends Component {


    state = {
        loading: true,
    }
    componentDidMount() {
        this.isLoading = setTimeout(() => { this.setState({ loading: false }) }, 1000);
    }
    componentWillUnmount() {
        clearTimeout(this.isLoading);
    }

    timer = () => setTimeout(() => {
        this.setState({ loading: false })
    }, 2300);


    render() {
        const { loading } = this.state;
        return (
            loading ? (
                <Loader style={{ marginTop: 240, textAlign: "center" }}
                    type="Oval"
                    color="#323A40"
                    height={200}
                    width={200}
                />
            ) : (
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-lg-12 col-sm-12 p-0">
                                <div style={{ backgroundColor: "#00063F", height: 50 }}><img className="logo" src={logo} /><SideNav></SideNav></div>
                                <div className="main-panel">{this.props.children}</div>
                            </div>
                        </div>
                    </div>
                )
        )
    }
}
export default Header;